    <div class="d-tabs">
      <div class="d-tabs-head">
        <div class="tab-nav">
          <div
            class="tab-nav-item"
            :class="{ active: tabCur == 0 }"
            @click="tabClick(0)"
          >
            密码管理
          </div>
          <div
            v-if="showtab"
            class="tab-nav-item"
            :class="{ active: tabCur == 1 }"
            @click="tabClick(1)"
          >
            银行卡管理
          </div>
          <div
          v-if="showtab"
            class="tab-nav-item"
            :class="{ active: tabCur == 2 }"
            @click="tabClick(2)"
          >
            USDT账号管理
          </div>
        </div>
      </div>
      <div class="d-tabs-content">
        <div class="tab-pane" label="usdt充值" v-if="tabCur == 0">
          <div class="pwd-wrapper">
            <!---->
            <div class="pwd">
              <div class="pwd-form">
                <h2 class="login-pwd" style="color: rgb(0, 229, 255)">
                  修改 [登录密码]
                </h2>
                <p>原登录密码:</p>
                <div>
                  <input
                    autocomplete="new-password"
                    placeholder="请输入登录的原密码"
                    type="password"
                    class="input"
                    v-model="oldLoginPwd"
                  />
                </div>
                <p>新登录密码</p>
                <div>
                  <input
                    autocomplete="new-password"
                    placeholder="登录的新密码由6-12位字母加数字组合"
                    type="password"
                    class="input"
                    v-model="newLoginPwd"
                  />
                </div>
                <h5>6-12位字母加数字组合，不能和提款密码相同</h5>
                <p>确认新密码</p>
                <div>
                  <input
                    autocomplete="new-password"
                    placeholder="再次输入登录的新密码"
                    type="password"
                    class="input"
                    v-model="newLoginPwd2"
                  />
                </div>
                <button class="button" @click="submitLogin">确认提交</button>
              </div>
            </div>
            <div class="pwd">
              <div class="pwd-form">
                <h2 class="draw-pwd">修改 [提款密码]</h2>
                <p>原提款密码:</p>
                <div>
                  <input
                    autocomplete="new-password"
                    placeholder="请输入提款的原密码"
                    type="password"
                    class="input"
                    v-model="oldPayPwd"
                  />
                </div>
                <p>新提款密码</p>
                <div>
                  <input
                    autocomplete="new-password"
                    placeholder="提款的新密码必须是6位数字"
                    type="password"
                    class="input"
                    v-model="newPayPwd"
                  />
                </div>
                <h5>6位数字，不允许连续三位相同，不能和登录密码相同</h5>
                <p>确认新密码</p>
                <div>
                  <input
                    autocomplete="new-password"
                    placeholder="再次输入提款的新密码"
                    type="password"
                    class="input"
                    v-model="newPayPwd2"
                  />
                </div>
                <button class="button" @click="submitPay">确认提交</button>
              </div>
            </div>
          </div>
        </div>
        <div class="tab-pane" label="银行卡管理" v-if="tabCur == 1">
          <div class="bank-card">
            <div style="height: 30px"></div>
            <div class="ds-table">
              <div class="table-title">
                <span>开户人姓名</span>
                <span>银行名称</span>
                <span>银行卡号</span>
              </div>
              <div class="table-content">
                <div class="table-row">
                  <span>{{ bankData.real_name }}</span>
                  <span>{{ bankData.bank_name }}</span>
                  <span>{{ bankData.bank_no }}</span>
                </div>
              </div>
            </div>
            <!---->
            <button
              class="button"
              @click="bindBankShow = true"
              v-if="!bankData.bank_no"
            >
              新增银行卡
            </button>
          </div>
        </div>

        <div class="tab-pane" label="USDT账号管理" v-if="tabCur == 2">
          <div class="bank-card">
            <div style="height: 30px"></div>
            <div class="ds-table">
              <div class="table-title">
                <span>USDT地址</span>
                <span>协议</span>
              </div>
              <div class="table-content">
                <div class="table-row">
                  <span></span>
                  <span></span>
                  <span></span>
                  <span></span>
                </div>
              </div>
            </div>
            <!---->
          </div>
        </div>
      </div>
    </div>

    <!-- 添加银行弹窗 -->
    <div class="shade" v-show="bindBankShow"></div>
    <div class="bind-card" v-show="bindBankShow">
      <div class="ds-close" @click="bindBankShow = false"></div>
      <h5>绑定银行卡</h5>
      <div class="card-content">
        <div class="card-item" style="position: relative">
          <label>开户银行:</label>
          <div class="ds-select">
            <input
              placeholder="请输入开户银行"
              class="input"
              v-model="bank_name"
            />
          </div>
        </div>
        <div class="card-item">
          <label>开户人姓名:</label>
          <input
            maxlength="10"
            minlength="2"
            placeholder="请输入开户人姓名"
            type="text"
            class="input"
            v-model="real_name"
          />
        </div>
        <div class="card-item">
          <label>开户支行:</label>
          <div class="ds-select">
            <input
              placeholder="请输入开户支行名称"
              class="input"
              v-model="branch"
            />
          </div>
        </div>
        <div class="card-item">
          <label>银行卡号:</label>
          <input
            autocomplete="new-password"
            placeholder="请输入您的银行卡号"
            type="number"
            class="input"
            v-model="bank_no"
          />
        </div>
      </div>
      <button
        class="button"
        style="margin: 0 auto; display: block"
        @click="submit"
      >
        提交
      </button>
    </div>
    <!-- 添加银行弹窗 -->
  </div>
</template>

<script>
import { getTxBank, bankTxAddEdit,xgPwd,xgDrawPwd,getUser } from "../api/public";
export default {
  data() {
    return {
      tabCur: 0,
      yhhdList: [],

      // 登录密码
      oldLoginPwd: "",
      newLoginPwd: "",
      newLoginPwd2: "",
      // 支付密码
      oldPayPwd: "",
      newPayPwd: "",
      newPayPwd2: "",
      // 绑定银行卡
      bankData: {},
      bindBankShow: false,
      bank_name: "",
      bank_no: "",
      real_name: "",
      branch: "",
      showtab: true,
    };
  },
  watch: {
    $route: "fetchData",
  },
  mounted() {
    this.userInfo = JSON.parse(localStorage.getItem("user_info"));
    console.log(this.$route.query.id)
    this.tabCur = this.$route.query.id || 0;

    this.getUser()
  },
  methods: {
     fetchData(newVal, oldVa) {
       this.userInfo = JSON.parse(localStorage.getItem("user_info"));
          console.log(oldVa)//oldVa 上一次url
          console.log(newVal);//newVal 这一次的url
            this.tabCur = newVal.query.id || 0;
           this.getUser()
        },
    getUser() {
      getUser({id: JSON.parse(localStorage.getItem('user_info')).id}).then(res => {		
        localStorage.setItem('user_info',JSON.stringify(res.data))	
        res.data.isSetDrawPwd==0 && 	(this.showtab = false,this.tabCur=0)
        if (res.code == 1 && this.$route.query.id==1) {
          if (res.data.isSetDrawPwd==0) {
            this.$message.error("请先设置提款密码");	
            this.tabCur = 0				
            
          } else if(res.data.isSetBank == 0) {
            this.$message.error("请先绑定提款银行卡");	
          } 
        }
      })
    },
    // 修改登录密码
    submitLogin() {
      if (!this.oldLoginPwd) {
        this.$message.error("请输入原登录密码");
        return false;
      }
      if (!this.newLoginPwd) {
        this.$message.error("请输入新登录密码");
        return false;
      }
      if (this.newLoginPwd2 != this.newLoginPwd) {
        this.$message.error("两次输入的密码不一致");
        return false;
      }
      xgPwd({
        id: this.userInfo.id,
        "old_pwd": this.oldLoginPwd,
		"new_pwd": this.newLoginPwd
      }).then((res) => {
        if (res.code == 1) {
          this.oldLoginPwd = "";
          this.newLoginPwd = "";
          this.newLoginPwd2 = "";          
          this.$message.success("修改成功");
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    // 修改提款密码
    submitPay() {
      if (!this.oldPayPwd && this.userInfo.isSetDrawPwd != 0) {
        this.$message.error("请输入原提款密码");
        return false;
      }
      if (!this.newPayPwd) {
        this.$message.error("请输入新提款密码");
        return false;
      }
      if (this.newPayPwd.length != 6) {
        this.$message.error("新提款密码必须6位");
        return false;
      }
      if (this.newPayPwd2 != this.newPayPwd) {
        this.$message.error("两次输入的密码不一致");
        return false;
      }
      xgDrawPwd({
        id: this.userInfo.id,
        "draw_pwd_old": this.oldPayPwd,
		"draw_pwd_new": this.newPayPwd
      }).then((res) => {
        if (res.code == 1) {
          this.oldPayPwd = "";
          this.newPayPwd = "";
          this.newPayPwd2 = "";          
          this.$message.success("修改成功");
          this.showtab = true
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    tabClick(i) {
      this.tabCur = i;
      this.$router.replace('/accountManagement?id='+this.tabCur)
      if (i == 1) {
        this.getBankList();
      }
    },
    getBankList() {
      getTxBank({
        id: this.userInfo.id,
      }).then((res) => {
        if (res.code == 1) {
          this.bankData = res.data;
          this.bankData.bank_no = this.bankData.bank_no.replace(
            /^(\d{4})\d+(\d{4})$/,
            "$1 **** **** $2"
          );
        }
      });
    },

    // 添加银行卡
    submit() {
      if (!this.bank_name) {
        this.$message.error("请填写开户行名称");
        return false;
      }
      if (!this.real_name) {
        this.$message.error("请填写开户人姓名");
        return false;
      }
      if (!this.branch) {
        this.$message.error("请填写开户支行");
        return false;
      }
      if (!this.bank_no) {
        this.$message.error("请填写银行卡号");
        return false;
      }
      bankTxAddEdit({
        user_id: this.userInfo.id,
        bank_name: this.bank_name,
        bank_no: this.bank_no,
        real_name: this.real_name,
        branch: this.branch,
      }).then((res) => {
        if (res.code == 1) {
          this.bank_name = "";
          this.bank_no = "";
          this.real_name = "";
          this.branch = "";
          this.bindBankShow = false;
          this.$message.success("提交成功");
        } else {
          this.$message.error(res.msg);
        }
      });
    },
  },
};
</script>

